import React from "react"
import Layout from "../components/layout"
import Project from "../components/project"
import webData from "../data/webData.js"

export default () => (
    <Layout>
    <div id="project-container">
        {webData.map(project =>
            <Project 
            info={project}
            key={project.id}
            /> )
        }
    </div>
    </Layout>
)